	module.exports = {"data":{"markdownRemark":{"html":"<p>Triggers are simple, but power tools for executing automated responses to new, incoming reports. You can use them to respond to a report where you frequently repeat yourself. It's best to not create overly broad triggers as false positives may annoy hackers. </p>\n<p>To set up triggers:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Automation > Triggers</strong>. </li>\n<li>Click <strong>Add new Trigger</strong>. </li>\n<li>Define the criteria for the trigger by entering keywords and selecting which part of the report must contain or not contain certain words. All criteria must match for the trigger to execute. </li>\n</ol>\n<p>by entering keywords and selecting what part of the report to search for those keywords. </p>","frontmatter":{"path":"/programs/triggers","title":"Triggers"}}},"pathContext":{}}

